      alert(id);
      // let newCartList = cartList.filter(function(element) {
      //   return element.id !== id;
      // });
      // cartList = newCartList;
    }
  },
  async created() {
    try {
      this.cart = await CartService.getCart();
    } catch (err) {
      this.error = err.message;
    }
  }
};
</script>

