<template >
  <div class="ui middle aligned stackable grid container cards">
    <div
      class="four wide column card"
      v-for="(item, index) in items"
      v-bind:item="item"
      v-bind:index="index"
      v-bind:key="item._id"
    >
      <img class="ui fluid image" v-bind:src="item.itemImgUrl" />
      {{item.itemImgUrl}}
      <h2>{{ item.itemName }}</h2>
      <div class="ui hidden divider"></div>
      <p>Lorem ipsum dolor, sit amet consectetur adipisicing elit. Qui, inventore.</p>
      <h2 class="ui sub header">Price</h2>
      <span>${{item.itemPrice}}</span>
      <div class="ui action input right floated">
        <div class="ui input">
          <input type="number" placeholder="1" />
        </div>
      </div>
      <div class="ui hidden divider"></div>
      <button class="ui inverted green button large fluid">Add to cart</button>
    </div>
  </div>
</template>

<script>
import ItemService from "../ItemService";
export default {
  name: "items",
  data() {
    return {
      items: [],
      error: "",
      text: ""
    };
  },
  async created() {
    try {
      this.items = await ItemService.getItems();
    } catch (err) {
      this.error = err.message;
    }
  }
};
</script>